/* div에 스타일 적용 */
.selectDiv {
    border: 1px solid #ccc; /* 회색 외곽선 */
    padding: 10px; /* 내용과 외곽선 사이 간격 */
    background-color: #f2f2f2; /* 배경색 설정 */
    width: 50%;
    max-width: 100%;
}

/* table에 스타일 적용 */
.playerTable {
    border-collapse: collapse; /* 테이블 셀 경계 통합 */
    border: 1px solid #ccc; /* 회색 외곽선 */
    width: 50%;
    max-width: 100%; /* 최대 너비를 100%로 설정 */
}

/* 테이블 행과 열에 외곽선 추가 */
.playerTable td, .playerTable th{
    border: none; /* 셀 내부의 모든 외곽선 제거 */
    padding: 8px; /* 셀 내용과 외곽선 사이 간격 */
}

/* 테이블 헤더에 배경색과 글자 색상 설정 */
.playerTable th {
    background-color: #f2f2f2; /* 배경색 설정 */
    padding: 8px; /* 헤더 셀 내용과 외곽선 사이 간격 */
}